.friends_container {

	 width: 100%;
	height: 100%; 
	overflow-y: scroll;
	padding: 10px 30px 0 15px ;
    /* box-shadow: 0 0  5px 5px #1ce14e; */

}

.friends_container::-webkit-scrollbar {
	width: 10px;
}

.friends_container::-webkit-scrollbar-thumb {
	background-color: #4caf50;
	border-radius: 6px;
}

.friends_container::-webkit-scrollbar-track {
	background-color: #e0e0e000;
}


